import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'


Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
  },
  {
    path: "/AddConsumer",
    name: "AddConsumer",
    /*route level code-splitting
    this generates a separate chunk (about.[hash].js) for this route
    which is lazy-loaded when the route is visited.*/
    component: () => import("../views/AddConsumer")
  },
  {
    path: '/addfile',
    name: "AddFile",
    component: ()=> import("../views/AddFile"),
  },
  {
    path: "/privatearea",
    name: "PrivateArea",
    component: () => import("../views/PrivateArea")
  },
  {
    path:"/consumers",
    name:"Consumers",
    component: () => import("../views/Consumers")
  },
  {
    path:"/files",
    name:"ManageFiles",
    component: ()=> import ("../views/Files")
  }
]

const router = new VueRouter({
  routes
})

export default router
