<template>
  <main>
    <h1>Welcome, {{ uploaderInfo.username }}!</h1>
    <p>
      You are an
      <em>{{ uploaderInfo.role }}</em> that have uploaded
      <em>{{ uploaderInfo.total_files }}</em>
      files and have a total of
      <em>{{ uploaderInfo.total_consumers }}</em> associated consumers. In
      HoneycombStorage you can add consumers that will receive the files you
      will upload. Enjoy HoneycombStorage platform now!
    </p>

    <section>
      <table>
        <tbody v-if="consumers.length !== 0">
          <tr v-for="consumer in consumers" v-bind:key="consumer.username">
            <td @click="changeActiveConsumer(consumer.username)">
              {{ consumer.username }}
            </td>
            <td>({{ consumer.files.length }})</td>
          </tr>
        </tbody>
        <tbody
          v-else
          style="display: flex; flex-direction: row; align-items: center"
        >
          <em style="text-align: center; flex-basis: 100%">
            You have not affiliate consumers
          </em>
        </tbody>
      </table>

      <ConsumerFilesTable
        v-if="activeConsumer != undefined"
        v-bind:files="activeConsumer.files"
      />
    </section>
  </main>
</template>

<script scoped>
import Axios from "axios";
import ConsumerFilesTable from "@/components/ConsumerFilesTable.vue";

export default {
  name: "Home",
  components: {
    ConsumerFilesTable,
  },

  methods: {
    changeActiveConsumer: function (username) {
      this.activeConsumer = this.consumers.filter(
        (consumer) => consumer.username === username
      )[0];
      //turn on the old consumer and turn on the new consumer
      document
        .querySelectorAll("table:first-of-type tbody tr td:first-child")
        .forEach((td) => {
          if (td.innerText === username) {
            td.style.color = "rgb(255, 255, 255)";
          } else {
            td.style.color = "rgb(214, 139, 0)";
          }
        });
    },
  },

  data: function () {
    return {
      uploaderInfo: {},
      consumers: [],
      activeConsumer: undefined,
    };
  },

  created: function () {
    Axios.get(`${process.env.VUE_APP_APIROOT}uploaderarea/generalinfo`)
      .then((response) => (this.uploaderInfo = response.data))
      .catch((error) => console.log(error));

    Axios.get(`${process.env.VUE_APP_APIROOT}uploaderarea/consumersextended`)
      .then((response) => {
        this.consumers = response.data;
        this.activeConsumer = this.consumers[0];
      })
      .catch((error) => console.log(error));
  },
};
</script>

